import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import MenuItem from '@material-ui/core/MenuItem';
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data, data1, page, search, sort } = props.table;
        let [art, setArt] = useState(selected!==-1?data[selected][1]:'');
        let handleArt =  (event) => {
            setArt(event.target.value)
        };
        let [line, setLine] = useState(selected!==-1?data[selected][2]:'');
        let handleLine =  (event) => {
            setLine(event.target.value)
        };
        let [material, setMaterial] = useState(selected!==-1?data[selected][3]:'');
        let handleMaterial =  (event) => {
            setMaterial(event.target.value)
        };
        let [weight, setWeight] = useState(selected!==-1?data[selected][4]:'');
        let handleWeight =  (event) => {
            setWeight(event.target.value)
        };
        let [price, setPrice] = useState(selected!==-1?data1[selected][5]:'');
        let handlePrice =  (event) => {
            setPrice(event.target.value)
        };
        let [count, setCount] = useState(selected!==-1?data1[selected][6]:'');
        let handleCount =  (event) => {
            setCount(event.target.value)
        };
        let [kategoty, setKategoty] = useState(selected!==-1?data[selected][7]:'');
        let handleKategoty =  (event) => {
            setKategoty(event.target.value)
        };
        let [podkategoty, setPodkategoty] = useState(selected!==-1?data[selected][8]:'');
        let handlePodkategoty =  (event) => {
            setPodkategoty(event.target.value)
        };
        let [status, setStatus] = useState(selected!==-1?data[selected][9]:'');
        let handleStatus =  (event) => {
            setStatus(event.target.value)
        };
        const statuses = ['нет в наличие', 'в наличие', 'отключен']
        let [level, setLevel] = useState(selected!==-1?data[selected][10]:'');
        let [keywords, setKeywords] = useState(selected!==-1?data[selected][11]:'');
        let handleKeywords =  (event) => {
            setKeywords(event.target.value)
        };
        let [description, setDescription] = useState(selected!==-1?data[selected][16]:'');
        let handleDescription =  (event) => {
            setDescription(event.target.value)
        };
        let [discount, setDiscount] = useState(selected!==-1?data[selected][12]:'');
        let handleDiscount =  (event) => {
            setDiscount(event.target.value)
        };
        let cod = selected!==-1?data[selected][13]:'';
        let [hit, setHit] = useState(selected!==-1?data[selected][15]:'');
        let handleHit =  (event) => {
            setHit(event.target.value)
        };
        const hits = ['включено', 'отключено']
        let [news, setNews] = useState(selected!==-1?data[selected][14]:'');
        let handleNews =  (event) => {
            setNews(event.target.value)
        };
        const newses = ['включено', 'отключено']
        let [file, setFile] = useState([]);
        let [fileNames, setFileNames] = useState(selected!==-1?data[selected][0]:'');
        let handleChangeFile = (async (event) => {
            setFile(event.target.files)
            let fileNames='';
            for(let i=0; i<event.target.files.length; i++){
                if(i!==0)
                    fileNames+=', '
                fileNames+=event.target.files[i].name+','
            }
            setFileNames(fileNames)
        })
        const { classes } = props;
        return (
            <div>
                <TextField
                    label='aртикул'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={art}
                    onChange={handleArt}
                />
                <br/>
                <TextField
                    label='линейка'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={line}
                    onChange={handleLine}
                />
                <br/>
                <TextField
                    label='материал'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={material}
                    onChange={handleMaterial}
                />
                <br/>
                <TextField
                    label='вес'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={weight}
                    onChange={handleWeight}
                />
                <br/>
                <TextField
                    label='цена'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={price}
                    onChange={handlePrice}
                />
                <br/>
                <TextField
                    label='количество'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={count}
                    onChange={handleCount}
                />
                <br/>
                <TextField
                    label='категория'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={kategoty}
                    onChange={handleKategoty}
                />
                <br/>
                <TextField
                    label='подкатегория'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={podkategoty}
                    onChange={handlePodkategoty}
                />
                <br/>
                <TextField
                    label='описание'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={description}
                    onChange={handleDescription}
                />
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='статус'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={status}
                    onChange={handleStatus}
                >
                    {
                        statuses.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                    }
                </TextField>
                <br/>
                <TextField
                    label='ключевые слова'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={keywords}
                    onChange={handleKeywords}
                />
                <br/>
                <TextField
                    label='скидка'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={discount}
                    onChange={handleDiscount}
                />
                <br/>
                <b>Код</b>
                <br/>
                {cod}
                <br/>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='хит'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={hit}
                    onChange={handleHit}
                >
                    {
                        hits.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='новинка'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={news}
                    onChange={handleNews}
                >
                    {
                        newses.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                    }
                </TextField>
                <br/>
                <div className={classes.urls}>
                    {fileNames}
                </div>
                <br/>
                <label htmlFor='contained-button-file'>
                    <Button variant='contained' color={fileNames.length>0?'primary':''} component='span' className={classes.button}>
                        Загрузить изображение
                    </Button>
                </label>
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                         if(selected===-1){
                             let count1 = count, count2 = [];
                             count1 = count1.split(';');
                             for (let i=0; i<count1.length; i++){
                                 if(count1[i].split(':')[1]!==undefined)
                                     count2[i] = {'color': count1[i].split(':')[0].replace('\n', '').trim(), 'kolichestvo': count1[i].split(':')[1].trim()}
                             }
                             let price1 = price, price2 = [];
                             price1 = price1.split(';');
                             for (let i=0; i<price1.length; i++){
                                 if(price1[i].split(':')[1]!==undefined)
                                     price2[i] = {'typeprice': price1[i].split(':')[0].replace('\n', '').trim(), 'price': price1[i].split(':')[1].trim()}
                             }
                             addData({
                                 search: search, sort: sort, page: page,
                                 name: 'Товары',
                                 file: file,
                                 data: {
                                     description: description,
                                     kategoria: kategoty,
                                     count: count2,
                                     material: material,
                                     line: line,
                                     price: price2,
                                     weight: weight,
                                     art: art,
                                     news: news,
                                     hit: hit,
                                     podkategoria: podkategoty,
                                     status: status,
                                     level: level,
                                     keyword: keywords,
                                     discount: discount
                                 }});
                         } else {
                            let count1 = count, count2 = [];
                            count1 = count1.split(';');
                            for (let i=0; i<count1.length; i++){
                                if(count1[i].split(':')[1]!==undefined)
                                    count2[i] = {'color': count1[i].split(':')[0].replace('\n', '').trim(), 'kolichestvo': count1[i].split(':')[1].trim()}
                            }
                             let price1 = price, price2 = [];
                             price1 = price1.split(';');
                             for (let i=0; i<price1.length; i++){
                                 if(price1[i].split(':')[1]!==undefined)
                                    price2[i] = {'typeprice': price1[i].split(':')[0].replace('\n', '').trim(), 'price': price1[i].split(':')[1].trim()}
                             }
                             setData({
                                 id: data[selected][18],
                                 search: search, sort: sort, page: page,
                                 name: 'Товары',
                                 oldFile: data[selected][0],
                                 file: file,
                                 data: {
                                     description: description,
                                     kategoria: kategoty,
                                     count: count2,
                                     material: material,
                                     line: line,
                                     price: price2,
                                     weight: weight,
                                     art: art,
                                     news: news,
                                     hit: hit,
                                     podkategoria: podkategoty,
                                     status: status,
                                     level: level,
                                     keyword: keywords,
                                     discount: discount
                                 }});
                         }
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
                <input
                    accept='image/*'
                    style={{ display: 'none' }}
                    id='contained-button-file'
                    multiple
                    type='file'
                    onChange={handleChangeFile}
                />
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));